@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.boddy{
  background: #000000;
}
.navbar{
  display: flex;
  flex-direction: column;
  background: #191919;
  font-family: Inter;
  color: #ebebeb;

}
.navbar-top{
  display: flex;
  align-items: center;
  align-content: center;
}
.search-option{
  margin: 1rem 0;
  border-radius: 10px;
  border-color: white;
  border-width: 0.01rem;
  padding: 0.4rem 0.8rem;
  color: #8c8ca1;
  font-size: 16px;
  background: black;
  font-family: "Work Sans";
}
.title-in{
  /* text-align: center; */
  display: flex;
  font-size: 16px;
}
.line{
  margin: 1rem auto;
  width: 100%;
  border-bottom: 0.1rem solid #575767;
}
li{
  font-size: 14px;
  list-style: none;
}
.hide{
  display: none;
}
.search-option:focus{
  outline: 0;
  border-color: #0086ff;
  border-width: 0.1rem;
  /* box-shadow: 0 1px 4px rgba(255, 255, 255, 0.8); */
}
.toggled-region{
  transition: height 5s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}
.navbar-image{
  position: absolute;
  left: 20px;
  top: 10px;
  height: 60px;
  width: auto;
  cursor: pointer;
}
.profile-card{
  display: none;
}
.power-attr{
  padding: 2rem;
  margin: 2rem;
  background: #191919;
  border: 0.1rem solid;
  border-radius: 25px;
  border-color: #575767;
}
.custom-checkbox
.custom-control-label::before {
  border-color: gray;
  border-radius: 0px;
  background-color: black;
}
.title-out{
  color: #cccdcd;
  font-family: Inter;
  font-size: 20px;
  text-align: center;
  font-weight: 600;
  margin: 1rem 0;
}
.vline{
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  border-bottom: 1px solid gray;
}
.ccol{
  display: flex;
  margin: 0.3rem 0.5rem;
}
.custom-control-label{
  color: white;
  font-size: 13px;
  font-weight: 600;
  font-family: Inter;
}
@media screen and (min-width:750px){
  .hide{
    display: inline;
  }
  .navbar-image{
    grid-area: hello;
    height: 100px;
    margin-left: 2.5rem;
    width: auto;
  }
  .navbar-top{
    position: absolute;
    left: 20px;
    display: inline;
  }
  .search-option{
    position: absolute;
    top: 130px;
  }
  .navbar-top span{
    position: absolute !important;
    top: 155px !important;
    left: 215px;
  }
  .navbar{
    height: 100vh;
    width: 260px;
    border-right: 0.01rem solid #8c8ca1;
  }
  .title-in{
    margin: 0.7rem 1rem;
  }
  li{
    margin: 0.20rem 0;
  }
  .toggled-region{
    position: absolute;
    left: 30px;
    top: 210px;
  }
  .profile-card{
    display: inline;
    margin-bottom: 1rem;
  }
  .profile-image{
    height: 60px;
    margin: 0 1.2rem 0 1rem;
    border-radius: 35px;
    border: 3px solid #0086ff;
  }
  .remaining{
    position: absolute;
    left: 270px;
    width: 80%;
    top: 20px;
  }
  .power-attr{
    display: flex;
    align-content: space-around;
    align-items: center;
  }
  .vline{
    margin: 0 3rem;
    border-left: 1px solid gray;
    height: 100px;
  }
  .custom-control-label{
    color: white;
    font-size: 15px;
    font-weight: 600;
    font-family: Inter;
  }
}
.card-container{
    width: 80vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-row-gap: 2rem;
    grid-column-gap: 20px;
}
.card-container img{
    margin: 2rem;
    margin-left: 4rem;
}
.card-container h1{
    position: absolute;
    margin-right: 1rem;
    color: orange;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    font-size: 2rem;
}
.myCard{
    margin: 2rem auto;
    border: 1px solid gray;
    display: block;
    background: #191919;
    border-radius: 15px;
    width: 300px;
}
.slider {
    -webkit-appearance: none;
    width: 100%;
    height: 5px;
    border-radius: 5px;  
    background: #dcdcdc;
    outline: none;
    opacity: 0.7;
    transition: opacity .2s;
  }
  
  .slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%; 
    background: white;
    cursor: pointer;
  }
  
  .slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
  }
.activated{
    background: #373737 !important;
    color: white !important;
    margin-left: 1.4rem !important;
    font-weight: 500;
    border-color: white;
}
.btn{
    color: #9898a0;
    background: #000;
    border-radius: 15px;
    font-family: Inter;
    font-weight: 600;
    margin: 1rem 0.5rem;
    /* border-color: #9898a0; */
}
.superpower{
    padding: 0.5rem 1.5rem;
    color: white;
    font-family: Inter;
}
.superpower h5{
    color: #575767;
    font-size: 13px;
    text-transform: uppercase;
}
.powers-list{
    list-style: none;
    display: flex;
    font-size: 15px;
    word-spacing: 0.2rem;
    text-transform: capitalize;
    padding: 0.5rem 0 2rem 0;
    border-bottom: 1px solid #575767;
}
.card-header{
    display: flex;
    margin: 1rem;
    align-content: center;
    align-items: center;
    color: #ebebeb;
}
.card-header img{
    height: 70px;
    width: 70px;
    border-radius: 50px;
    border: 3px solid;
    border-color: #0086ff;
    margin-right: 30px;
}
.card-header h3{
    margin-top: 0.4rem;
    font-size: 22px;
}
